import React, { Component } from 'react';
import Chart from './Chart'
import StatisticsMenu from './StatisticsMenu';


export class Statistics extends Component {

    render() {

        return (
            <div className="container">
                <div className="row justify-content-around">
                    <div className="col-3">
                        <StatisticsMenu />
                    </div>
                    <div className="col-8">
                        <div className="row">
                            <div className="col-6 input-group">
                                <div class="input-group-prepend">
                                    <label class="input-group-text" for="inputGroupSelect01">Od:</label>
                                </div>
                                <select className="custom-select">
                                    <option selected="">Dzień</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                                <select className="custom-select">
                                    <option selected="">Miesiąc</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                                <select className="custom-select">
                                    <option selected="">Rok</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                            </div>

                            <div className="col-6 input-group">
                                <div class="input-group-prepend">
                                    <label class="input-group-text" for="inputGroupSelect01">Do:</label>
                                </div>
                                <select className="custom-select">
                                    <option selected="">Dzień</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                                <select className="custom-select">
                                    <option selected="">Miesiąc</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                                <select className="custom-select">
                                    <option selected="">Rok</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                            </div>
                        </div>

                        <br></br>
                        <Chart labels={this.props.labels} numbers={this.props.numbers} type={this.props.type} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Statistics;