import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link
} from 'react-router-dom';
import './App.css';
import NavBar from './components/NavBar';
import Statistics from './components/Statistics';
import Diagnostic from './components/Diagnostic';
import Porter from './components/Porter';
import Administrator from './components/Administrator';



function App() {
  return (
    <div >
      <Router>
        {/*<NavBar />*/}
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
          <a className="navbar-brand" href="/">System liczenia frekwencji</a>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item active"><Link to="./statistics">Analityka</Link><span className="sr-only">(current)</span></li>
              <li className="nav-item"><Link to="./diagnostic">Diagnostyka</Link></li>
              <li className="nav-item"><Link to="./portier">Portier</Link></li>
              <li className="nav-item"><Link to="./administrator">Administrator</Link></li>
            </ul>
          </div>
        </nav>
        <div className="row justify-content-around">

          <Switch>
            <Route exact path="/statistics">
              <Statistics
                labels={["Dzień 1", "Dzień 2", "Dzień 3", "Dzień 4", "Dzień 5", "Dzień 6", "Dzień 7"]}
                numbers={[35, 48, 60, 54, 56, 83, 42]}
                type={"D"} />
            </Route>
            <Route path="/diagnostic">
              <Diagnostic />
            </Route>
            <Route path="/portier">
              <Porter />
            </Route>
            <Route path="/administrator">
              <Administrator />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;