import React, { Component } from "react";

class Porter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            frequencyInMinutes: 1,
        };
    }

    render() {
        return (
            <div className="container">
                <div className="row justify-content-around">
                    <div className="col-6">
                        <form>
                            <div class="form-group">
                                <label for="exampleSelect2">Co ile minut liczba gości ma być aktualizowana?</label>
                                <select multiple="" class="form-control" id="exampleSelect2">
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                    <option>15</option>
                                    <option>30</option>
                                    <option>60</option>
                                </select>
                            </div>
                        </form>
                    </div>
                    <div className="col-6">
                        <div class="alert alert-dismissible alert-info">
                            <strong>Aktualna liczna zwiedzających:</strong><br></br> 
                            <h3>34</h3>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Porter;