import React, { Component } from "react";

class StatisticsMenu extends Component {
    render() {
        return (
            <div class="list-group">
                <a href="/today" class="list-group-item list-group-item-action active disabled">Statystyki z wybranego okresu:</a>
                <a href="/tomorrow" class="list-group-item list-group-item-action">Statystyki z dzisiaj</a>
                <a href="/yesterday" class="list-group-item list-group-item-action">Statystyki z wczoraj</a>
                <a href="/week" class="list-group-item list-group-item-action">Statystyki z tygodnia</a>
                <a href="/month" class="list-group-item list-group-item-action">Statystyki z miesiąca</a>
                <a href="/year" class="list-group-item list-group-item-action">Statystyki z roku</a>
                <a href="/all" class="list-group-item list-group-item-action">Statystyki z całego okresu</a>
                <a href="/unstandard" class="list-group-item list-group-item-action">Statystyki z okresu niestandardowego</a>
            </div>
        );
    }
}

export default StatisticsMenu;